.youtube > .inner {
	.youtubeList {
		margin-bottom: 100px;

		article {
			width: 100%;
			height: 500px;
			position: relative;
			border-top: 1px solid #222;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			padding: 50px 0;

			&:last-child {
				border-bottom: 1px solid #222;
			}

			> .pic {
				width: 60%;
				height: 400px;
				position: relative;
				cursor: pointer;

				&::after {
					content: '▶';
					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					font-size: 50px;
					color: #fff;
					opacity: 0;
					transition: 0.3s;
				}
				&::before {
					content: '';
					display: block;
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: #6fd885;
					opacity: 0;
					transition: 0.3s;
				}

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					transition: 0.3s;
				}

				&:hover {
					&::after {
						opacity: 0.9;
						z-index: 2;
					}
					&::before {
						opacity: 0.5;
						z-index: 1;
					}
					img {
						filter: grayscale(1);
					}
				}
			}

			> .txt {
				width: 38%;
				height: 400px;
				position: relative;

				h2 {
					display: block;
					margin-bottom: 20px;
					font: 16px/1 'Smooch Sans';
					color: #555;
				}
				h3 {
					font: bold 55px/1.2 'Smooch Sans';
					color: #222;
				}
				p {
					position: absolute;
					bottom: 50px;
					font: 18px/1.5 'Smooch Sans';
					color: #222;
				}
				.view {
					display: block;
					position: absolute;
					bottom: 0;
					font: bold 16px/1 'Smooch Sans';
					color: #555;
					border-bottom: 2px solid #555;
					padding-bottom: 2px;
					cursor: pointer;

					&::after {
						content: '→';
						display: block;
						color: #555;
						position: absolute;
						bottom: 0;
						left: 50px;
						font-size: 16px;
						color: #6fd885;
						opacity: 0;
						transition: 0.5s;
					}
					&:hover {
						color: #6fd885;
						border-bottom: 2px solid #6fd885;

						&::after {
							left: 60px;
							opacity: 1;
						}
					}
				}
			}

			&:nth-of-type(2n) {
				> .pic {
					position: absolute;
					right: 0;
				}
				> .txt {
					position: absolute;
					left: 0;
				}
			}
		}
	}

	.metaverse {
		> div {
			min-height: 300px;
		}
	}
}

@media screen and (max-width: $tablet) {
	.youtube > .inner {
		.youtubeList {
			article {
				height: auto;
				display: block;

				> .pic {
					width: 100%;
					height: 260px;
					margin-bottom: 30px;
				}

				> .txt {
					width: 100%;
					height: auto;

					h3 {
						font-size: 40px;
						margin-bottom: 60px;
					}
					p {
						position: relative;
					}
				}

				&:nth-of-type(2n) {
					> .pic {
						position: relative;
					}
					> .txt {
						position: relative;
					}
				}
			}
		}
	}
}

@media screen and (max-width: $mobile) {
	.youtube > .inner {
		.youtubeList {
			article {
				> .pic {
					height: 140px;
				}

				> .txt {
					width: 100%;
					height: auto;

					h3 {
						font-size: 30px;
					}
					p {
						font-size: 14px;
					}
				}
			}
		}
	}
}
